  max-width: calc(33.3333333333% - var(--grid-gutter));
}

.col-5 {
  flex: 0 0 calc(41.6666666667% - var(--grid-gutter));
  max-width: calc(41.6666666667% - var(--grid-gutter));
}

.col-6 {
  flex: 0 0 calc(50% - var(--grid-gutter));
  max-width: calc(50% - var(--grid-gutter));
}

.col-7 {
