:host, :root {
  --bg-color: #fff;
  --bg-secondary-color: #f3f3f6;
  --color-primary: #3e63ad;
  --color-lightGrey: #d2d6dd;
  --color-grey: #3e9a80;
  --color-darkGrey: #80669d;
  --color-error: #d43939;
  --color-success: #33b249;
  --grid-maxWidth: 150rem;
  --grid-gutter: 1rem;
